*{
  margin: 0;
  padding: 0;
}

.main_div{  
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; 
 background: #6983aa;
 width: 100%;
 height: 100vh;
 
}
.center_div{
  width: 25%;
  height:80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

.heading{
  color: #fff;
  background-color: rgb(94, 16, 173);
  text-align: center;
  margin-bottom: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
  
}
input{
  text-align: center;
  
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  
  width: 50%;
  border-bottom: 2px solid #8566aa;
  outline: none;

}
button{
  height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8566aa;
  color: #fff;
  font-size: 31px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.3);
}
button:hover{
  background-color: green;
}
.container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
ol{
  margin-top: 30px;
}

ol li{
 font-size: 20px;
 font-weight: 500;
 display: flex;
 align-items: center;
 color: #8566aa;
 
}

.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}
.todo_style .fa-times:hover{
  background-color: crimson;
}

@media (max-width: 768px) {
  .center_div{
    width: 79%;
  }

  .todo_style .fa-times{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  button{
    margin-left: 50px;
    font-size: 30px;
  }

  
  
}
